import React from 'react'
import { StaticQuery, graphql, Link } from 'gatsby'

import Layout from '../components/layout'

const SecondPage = () => (
  <Layout>
    <StaticQuery
      query={graphql`
        query SecondPageQuery {
          allPlayersJson {
            edges {
              node {
                playerName
                team
                points
              }
            }
          }
        }
      `}
      render={data => {
        const players = data.allPlayersJson.edges.map((el) => {
          return el.node
        });
        console.log(players);
        return (
          <div>
            <ul>
              {
                players.map((player) => {
                  return (
                    <li key={player.playerName}><Link to={`/${player.playerName}/`}>{player.team} {player.playerName}</Link></li>
                  )
                })
              }
            </ul>
          </div>
        )
      }}
    />
  </Layout>
)

export default SecondPage
